<script>
    import "@fortawesome/fontawesome-free/css/all.css";
    import { onMount } from "svelte";
    import ReportsTable from "../components/ReportsTable.svelte";
    import AxleCounterTable from "../components/AxleCounterTable.svelte";
    import DatePicker from "../components/DatePicker.svelte";
    import Loader from "../components/Loader.svelte";
    import config from "../const.js";
    import { pretty_date, human_date } from "../pretty.js";
    import "../middle_ware.js";
    import { axle_query } from "../stores.js"
    import { pad_int } from "../lib.js";

    let reports;
    let axle_data;
    let query = "";
    let show_to_picker = false;
    let show_from_picker = false;
    let is_uploading = false;
    let is_downloading = false;
    let to_date = new Date();
    let from_date = new Date();
    from_date.setDate(to_date.getDate() - 30);
    let file_upload_element;

    const sort_methods = {
        number_of_crossings: {
            asc: (a, b) => a.number_of_crossings || Number.MAX_SAFE_INTEGER - b.number_of_crossings || Number.MAX_SAFE_INTEGER,
            desc: (a, b) => b.number_of_crossings || Number.MAX_SAFE_INTEGER - a.number_of_crossings || Number.MAX_SAFE_INTEGER,
        },
        axle_counter: {
            asc: (a, b) => a.localeCompare(b),
            desc: (a, b) => b.localeCompare(a),
        },
        ignored: {
            asc: (a, b) => a.ignored - b.ignored,
            desc: (a, b) => b.ignored - a.ignored,
        },
        days_since_last_crossing: {
            asc: (a, b) => new Date(a.crossing_time) - new Date(b.crossing_time),
            desc: (a, b) => new Date(b.crossing_time) - new Date(a.crossing_time),
        },
    }

    function format_date(date) {
        const year = date.getFullYear();
        const month = date.getMonth() + 1;
        const day = date.getDate();
        const hours = date.getHours();
        const minutes = date.getMinutes();
        const seconds = date.getSeconds();

        return `${year}-${pad_int(month)}-${pad_int(day)} ${pad_int(hours)}:${pad_int(minutes)}:${pad_int(seconds)}`;
    }

    function show_from_date_picker() {
        show_to_picker = false;
        show_from_picker = true;
    }

    function show_to_date_picker() {
        show_to_picker = true;
        show_from_picker = false;
    }

    function from_date_picked(date) {
        from_date = date;
        show_from_picker = false;
        reports = fetch(`${config.api_url}/axle_counters/report?from=${format_date(from_date)}&to=${format_date(to_date)}`).then(res => {
            return res.json();
        }).then(reports => {
            reports.sort((a, b) => {
                return new Date(a.crossing_time) - new Date(b.crossing_time);
            });
            for (const report of reports) {
                report.crossing_time = pretty_date(
                    report.crossing_time
                ) || "No crossing time avaliable";
            }
            return reports;
        });
    }

    function to_date_picked(date) {
        to_date = date;
        show_to_picker = false;
        reports = fetch(`${config.api_url}/axle_counters/report?from=${format_date(from_date)}&to=${format_date(to_date)}`).then(res => {
            return res.json();
        }).then(reports => {
            reports.sort((a, b) => {
                return new Date(a.crossing_time) - new Date(b.crossing_time);
            });
            for (const report of reports) {
                report.crossing_time = pretty_date(
                    report.crossing_time
                ) || "No crossing time avaliable";
            }
            return reports;
        });
    }

    function download_report() {
        is_downloading = true;

        fetch(`${config.api_url}/axle_counters/report/excel?from=${format_date(from_date)}&to=${format_date(to_date)}`, {
            method: "GET",
        }).then((response) => {
            return response.blob();
        }).then((blob) => {
            const url = window.URL.createObjectURL(blob);
            const a = document.createElement("a");
            a.href = url;
            a.download = "axle_counter_report.xlsx";
            document.body.appendChild(a);
            a.click();
            a.remove();
        }).catch((error) => {
            console.error(error);
        }).finally(() => {
            is_downloading = false;
        });
    };

    function upload_file_selected() {
        const selected_file = file_upload_element.files[0];

        if (!config.allowed_file_types.includes(selected_file.type)) {
            console.error("Invalid file type");
            return;
        }

        is_uploading = true;
        const data = new FormData()
        data.append("file", selected_file)

        fetch(`${config.api_url}/import_excel/`, {
                method: "POST",
                body: data,
        }).then(response => {
            response.json()
        }).catch((err) => {
            console.error(err)
        }).finally(() => {
            is_uploading = false;
        });
    }

    onMount(() => {
        reports = fetch(`${config.api_url}/axle_counters/report?from=${format_date(from_date)}&to=${format_date(to_date)}`).then(res => {
            return res.json();
        }).then(reports => {
            reports.sort((a, b) => {
                return new Date(a.crossing_time) - new Date(b.crossing_time);
            });
            for (const report of reports) {
                report.crossing_time = pretty_date(
                    report.crossing_time
                ) || "No crossing time avaliable";
            }
            return reports;
        });
    });
</script>

<svelte:head>
    <title>Axle Counter</title>
</svelte:head>

<input
    bind:this={file_upload_element}
    on:change={upload_file_selected}
    type="file"
    style="width: 0; height: 0;"
>
<div class="main-container">
    <div class="frow">
        <div class="header">
            <strong>axle</strong>
            counter
        </div>
    </div>

    <div class="content">
        <div class="content-header">
            <span class="title">axle counter reports</span>
            <button on:click={() => file_upload_element.click()} title="Upload axle counter data">
                <span>
                    {#if is_uploading}
                        <Loader index={Math.floor((Math.random() * 42) + 1)} scale={1} color={"white"} />
                    {:else}
                        <i class="fas fa-upload"></i>
                    {/if}
                </span>
            </button>
            <button on:click={download_report} title="Download axle counter data">
                <span>
                    {#if is_downloading}
                        <Loader index={Math.floor((Math.random() * 42) + 1)} scale={1} color={"white"} />
                    {:else}
                        <!-- <i class="fas fa-download"></i> -->
                        <!-- <div>Icons made by <a href="https://www.flaticon.com/authors/dave-gandy" title="Dave Gandy">Dave Gandy</a> from <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a> is licensed by <a href="http://creativecommons.org/licenses/by/3.0/"                 title="Creative Commons BY 3.0" target="_blank">CC 3.0 BY</a></div> -->
                        <img src="icons/download.svg" alt="download" width="19" height="19" display="fill: white;">
                    {/if}
                </span>
            </button>
            <div
                class="pseudo-input"
                on:click={show_from_date_picker}
                title="Set a minimum date for axle counter data"
            >
                {human_date(from_date)}
            </div>
            <div class="floater" class:hidden={!show_from_picker}>
                <DatePicker
                    on:date={(e) => from_date_picked(e.detail.date)}
                    on:cancel={() => show_from_picker = false}
                />
            </div>
            <span class="pseudo-input-lbl">TO</span>
            <div
                class="pseudo-input"
                on:click={show_to_date_picker}
                title="Set a maximum date for axle counter data"
            >
                {human_date(to_date)}
            </div>
            <div class="floater" class:hidden={!show_to_picker}>
                <DatePicker
                    on:date={(e) => to_date_picked(e.detail.date)}
                    on:cancel={() => show_to_picker = false}
                />
            </div>
            <input
                type="text"
                placeholder="Search"
                bind:value={query}
                on:input={() => axle_query.sedust(query)} title="Filter axle counters"
            >
        </div>
        <div class="inner-content">
            {#if reports}
                {#await reports}
                    <Loader index={Math.floor((Math.random() * 42) + 1)} scale={8} color={"#689dc0"} />
                {:then counters}
                    <AxleCounterTable
                        counters={counters}
                        aliases={config.axle_counter_aliases}
                        sorting={sort_methods}
                        sortby={"days_since_last_crossing"}
                        ascending={false}
                        threshold={22}
                        exclude={["id", "ignored", "ignore", "reset_tvs", "days_since_last_crossing", "crossing_time"]}
                    />
                {:catch error}
                    <div class="error-box">
                        <span>No data was avaliable</span>
                        <span>{error}</span>
                    </div>
                {/await}
            {/if}
        </div>
    </div>
</div>

