<script>
    import { reports_query } from "../stores.js";

    export let reports = [];
    export let exclude = [];
    export let headers = {};
    export let page_index = 0;
    export let maxlen = 7;

    $: filtered_reports = reports.filter((report, index) => {
        return (report.axle_counter.toLowerCase().includes($reports_query.toLowerCase())
            || `${report.number_of_crossings}`.includes($reports_query)
            || report.crossing_time.includes($reports_query)
            || $reports_query === "")
        && index >= page_index * maxlen
        && index <= page_index * maxlen + maxlen;
    });

    $: queried_reports = reports.filter((report, index) => {
        return (report.axle_counter.includes($reports_query)
            || `${report.number_of_crossings}`.includes($reports_query)
            || report.crossing_time.includes($reports_query)
            || $reports_query === "")
    });

    $: last_page = Math.floor(queried_reports.length / maxlen);

    $: {
        page_index = page_index > last_page ? last_page : page_index;
    }

    function next() {
        if (page_index >= last_page) {
            return;
        }
        page_index = page_index >= last_page ? last_page : page_index + 1;
    }

    function prev() {
        if (page_index <= 0) {
            return;
        }
        page_index = page_index <= 0 ? 0 : page_index - 1;
    }
</script>

<table>
    <thead>
        <tr>
            {#each Object.keys(reports[0]) as key}
                {#if !exclude.includes(key)}
                    <th>
                        {#if Object.keys(headers).includes(key)}
                            {headers[key]}
                        {:else}
                            {key}
                        {/if}
                    </th>
                {/if}
            {/each}
        </tr>
    </thead>
    <tbody>
        {#each filtered_reports as report}
            <tr>
                {#each Object.keys(report) as key}
                    {#if !exclude.includes(key)}
                        <td>{report[key]}</td>
                    {/if}
                {/each}
            </tr>
        {/each}
    </tbody>
</table>

<div class="table-nav">
    <div class="center-wrap">
        <button
            class:disabled={page_index <= 0}
            on:click={prev}
        >prev</button>
        <span>Page {page_index + 1} of {last_page + 1}</span>
        <button
            class:disabled={page_index >= last_page}
            on:click={next}
        >next</button>
    </div>
</div>



