    text-transform: initial; }
    table tbody tr {
      background-color: white; }
      table tbody tr:nth-child(odd) {
        background-color: #F3F3F3; }
      table tbody tr td {
        font-size: 20px;
        height: 34px; }
        table tbody tr td:last-child {
          display: flex;
          justify-content: center; }
        table tbody tr td .shoveling-btn {
          display: flex;
          justify-content: center;
          height: 28px;
          text-transform: uppercase;
          margin: 3px 0;
          border: none;
          color: white;
          background-color: inherit;
          cursor: pointer;
          padding: 0; }
          table tbody tr td .shoveling-btn:hover i {
            font-size: 18px;
            background-color: #0068d2; }
          table tbody tr td .shoveling-btn:hover span {
            color: #0068d2;
            border-color: #0068d2; }
          table tbody tr td .shoveling-btn i {
            color: white;
            background-color: #0681ff;
            font-size: 18px;
            width: 28px;
            height: 28px;
            line-height: 28px; }
          table tbody tr td .shoveling-btn span {
            font-size: 14px;
            padding: 0 5px;
            line-height: 28px;
            color: #0681ff;
            border: 1px solid #0681ff;
            border-left: none; }
        table tbody tr td .no-last-crossing {
          font-weight: bold;
          color: #f32121; }

.table-nav {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: flex-end;
  flex: 1; }
  .table-nav .center-wrap {
    display: flex;
    justify-content: center;
    align-items: center; }
    .table-nav .center-wrap button {
      cursor: pointer;
      padding: 3px;
      width: 80px;
      font-size: 16px;
      text-transform: uppercase;
      border: none;
      background-color: #0681ff;
      color: white; }
      .table-nav .center-wrap button:not(.disabled):hover {
        background-color: #0068d2; }
