      font-size: 20px;
      margin: 0 20px; }

.submit-shoveling {
  width: 100%;
  margin-top: 15px;
  background-color: #0681ff;
  color: white;
  border: none;
  font-size: 22px;
  padding: 5px; }
  .submit-shoveling.disabled {
    background-color: #888; }
  .submit-shoveling:not(.disabled) {
    cursor: pointer; }
    .submit-shoveling:not(.disabled):hover {
      background-color: #0068d2; }

