    .datepicker .header .next {
      text-align: right; }
      .datepicker .header .next .month {
        margin-right: 5px; }
    .datepicker .header .prev {
      text-align: left; }
      .datepicker .header .prev .month {
        margin-left: 5px; }
  .datepicker table {
    width: 100%; }
    .datepicker table * {
      text-align: center; }
    .datepicker table td {
      width: 35px;
      height: 35px;
      font-size: 16px; }
      .datepicker table td:not(.week-number) {
        cursor: pointer; }
        .datepicker table td:not(.week-number).today {
          background-color: #7dabca; }
        .datepicker table td:not(.week-number):hover, .datepicker table td:not(.week-number).picked {
          background-color: #0681ff;
          color: white; }
        .datepicker table td:not(.week-number).disabled:not(.picked):not(.today) {
          color: #AAA; }
      .datepicker table td.week-number {
        font-weight: bold; }

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px; }
  .footer .timepicker {
    display: flex;
    align-items: center;
